<template>
  <div class="show">
    <el-button type="primary" round @click="table = true">
      逻辑展示&nbsp;<el-icon><Pointer /></el-icon>
    </el-button>
  </div>
  <el-drawer v-model="table" title="逻辑展示" direction="btt" size="75%">
    <csTitle title="棋盘搭建"/>
    <csText :text="text1"/>
    <csCode :code="code" type="js" />
    <csText :text="text2"/>
    <csText :text="text3"/>
  </el-drawer>
</template>

<script setup>
import { csCode,csTitle,csText } from "@/components/codeShow";
import { Pointer } from "@element-plus/icons-vue";
import { ref } from "vue";
const text1 = "创建一个二维数组的方法，数组的尺寸由 form.size 和 maxPx.value 决定"
const text2 = "先来看一下 Array.from() 这个函数。Array.from() 方法从一个类似数组或可迭代对象中创建一个新的、浅拷贝的数组实例。这里的 { length: form.size } 是一个含有 length 属性的对象，Array.from() 会将其视为数组并创建一个长度为 form.size 的新数组。"
const text3 = "Array.from() 的第二个参数是一个映射函数，用于对每个元素进行处理。在这里，它返回另一个数组 Array(form.size > maxPx.value ? maxPx.value : form.size).fill('')。这个数组的长度由 form.size > maxPx.value ? maxPx.value : form.size 这个三元运算符决定，即如果 form.size 大于 maxPx.value，则长度为 maxPx.value，否则为 form.size。然后 .fill('') 将数组填充为空字符串。"
const table = ref(false);
const code = `Array.from({ length: form.size }, () =>
    Array(form.size > maxPx.value ? maxPx.value : form.size).fill("")
);`
</script>

<style lang="less" scoped>
.show {
  max-width:350px ;
  width: 80%;
  display: flex;
  align-items: start;
  margin-bottom: 10px;
}
</style>
