<!-- 需求：
1.三子连珠提示（可选）
2.五子连珠显示胜利棋子
3.校验规则优化 
4.格子优化 棋子下在交叉线上
5.悔棋功能添加-->
<template>
  <div class="container">
    <div class="checkerboardTitle">
      <div class="prompt">{{ content[arrayIndex] }}</div>
      请落子
      <el-button type="primary" @click="none = true" v-if="!none"
        >显示</el-button
      >
    </div>
    <!-- 棋盘 -->
    <div class="checkerboard">
      <div v-for="(row, i) in list" :key="i" class="row">
        <div v-for="(call, j) in row" :key="j" class="col" @click="play(i, j)">
          {{ call }}
        </div>
      </div>
    </div>
    <div class="function-buttons" v-if="none">
      <el-button type="primary" @click="setUp = true">设置</el-button>
      <el-button type="primary" @click="none = false" v-if="none"
        >隐藏</el-button
      >
      <el-button type="primary" @click="length()">清空</el-button>
      <returnButton></returnButton>
    </div>
  </div>
  <!-- 设置 -->
  <el-dialog v-model="setUp" title="棋盘设置" width="350px">
    <el-form label-width="120px" :model="form">
      <el-form-item label="棋盘大小:">
        <el-select v-model.number="form.size">
          <el-option value="9" label="9" />
          <el-option value="16" label="16" />
          <el-option value="25" label="25" />
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submit()">确定</el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
  <!-- 胜利提示 -->
  <victoryDialog
    :length="length"
    :content="content[(arrayIndex + 1) % 2]"
    v-model="victory"
  ></victoryDialog>
</template>

<script setup>
import returnButton from "@/components/returnButton";
import victoryDialog from "./victoryDialog";
import { onMounted, reactive, ref } from "vue";
const form = reactive({
  size: 9,
});
const setUp = ref(true);
const content = ["X", "O"];
const victory = ref(false);
const list = ref([]);
const maxPx = ref();
const none = ref(true);
let arrayIndex = 0;
onMounted(() => {
  length();
  maxPx.value = Math.round(window.innerWidth / 31 - 2);
});
const length = () => {
  console.log(maxPx.value);
  list.value = Array.from({ length: form.size }, () =>
    Array(form.size > maxPx.value ? maxPx.value : form.size).fill("")
  );
  if (victory.value === true) {
    victory.value = false;
  }
  arrayIndex = 0;
};
const play = (i, j) => {
  if (list.value[i][j] !== "") return;
  list.value[i][j] = content[arrayIndex];
  arrayIndex++;
  if (arrayIndex > 1) {
    arrayIndex = 0;
  }
  rule(i, j);
};
const submit = () => {
  length();
  setUp.value = false;
};
/**
 * 第一版教研规则 全棋盘验证 若棋盘够大会印象 内存
 * list.value[i][j]
 * [i]代表y轴
 * [j]代表x轴
 * 横向判断 纵向判断 左斜/右斜判断
 */
const rule = (i, j) => {
  let arr = [];
  const ruleArr = (array) => {
    if (array.every((res) => res === content[(arrayIndex + 1) % 2])) {
      victory.value = true;
    }
  };
  for (let a = 0; a < list.value[i].length - 4; a++) {
    arr = [
      list.value[i][a],
      list.value[i][a + 1],
      list.value[i][a + 2],
      list.value[i][a + 3],
      list.value[i][a + 4],
    ];
    ruleArr(arr);
    arr = [
      list.value[a][j],
      list.value[a + 1][j],
      list.value[a + 2][j],
      list.value[a + 3][j],
      list.value[a + 4][j],
    ];
    ruleArr(arr);
    for (let b = 0; b < list.value[i].length - 4; b++) {
      arr = [
        list.value[a][b],
        list.value[a + 1][b + 1],
        list.value[a + 2][b + 2],
        list.value[a + 3][b + 3],
        list.value[a + 4][b + 4],
      ];
      ruleArr(arr);
    }
    for (let b = list.value[i].length - 1; b > 3; b--) {
      arr = [
        list.value[a][b],
        list.value[a + 1][b - 1],
        list.value[a + 2][b - 2],
        list.value[a + 3][b - 3],
        list.value[a + 4][b - 4],
      ];
      ruleArr(arr);
    }
  }
};
</script>

<style lang="less" scoped>
.checkerboardTitle {
  font-size: 30px;
  color: #409eff;
  margin-bottom: -20px;
  font-weight: bolder;
}
